"use client"
import Image from "next/image"
import {motion} from 'framer-motion'



export default function SectionTwo() {

    return <div className="  flex space-x-4  backdrop-blur-lg backdrop-saturate-[100%] m-10 rounded-3xl  bg-cyan-300/10  border-2 border-white  bg-opacity-10 p-10">
            <div className="  flex space-x-4  backdrop-blur-lg backdrop-saturate-[100%] m-2 rounded-3xl   bg-cyan-100/10  border-2 border-white  bg-opacity-10 p-10 "> 
             <div className="flex flex-col space-x-3 p-10">
                <div className="text-4xl text-purple-800 p-5 font-bold ">  Get Insights About  </div>
               
                <div className="flex flex-col space-y-4"> 
                {[
                
                {
                            
                  title: "Inner world" },{
                            
                    title: "Inspiration" },{
                            
                    title: "Lyrical Interpretation" },
                                  {
                           
                            title: "Thoughts",
                                  },].map((feature, index) => (
                            <motion.div
                              key={index}
                                  initial={{ opacity: 0, y: 20 }}
                                  whileInView={{ opacity: 1, y: 0 }}
                                  transition={{ delay: index * 0.1 }}
                              
                              className="flex flex-col   bg-white bg-opacity-10 border border-opacity-20 border-[#ffffff] items-start p-4 md:p-6 bg-primary rounded-xl shadow-lg hover:shadow-2xl transition-all duration-300"
                            >
                              
                              <h3 className="text-lg md:text-xl font-lora font-bold mb-2 text-purple-300">{feature.title}</h3>
                                 </motion.div>
                          ))}
                          </div> 
             </div>
             <div className=" flex flex-col space-y-7 p-10 bg-white/95 backdrop-blur-md rounded-2xl">
              <div className="flex space-x-7">
                <div>
                    <Image alt="pic1" className="rounded-3xl" width={1200} height={1000}  src={'/pic1.svg'}/>

                </div>
                <div>
                <Image alt="pic1" className="rounded-3xl" width={1000} height={800} src={'/pic2.svg'}/>
                </div>
              </div >
              <div  className="flex space-x-7">
                <div>
                <Image alt="pic1" className="rounded-3xl " width={1000} height={800} src={'/pic3.svg'}/>
                </div>
                <div>
                <Image alt="pic1" className="rounded-3xl" width={1200} height={1000} src={'/pic4.svg'}/>
                </div>

              </div>
               
             </div>
    </div>
    </div>
}